<template>
  <div class="fill-information-container">
    <!--    -->
    <!--    -->
    <!--        -->
    <!--      -->
    <a-form-model ref="infoForm" class="info-form"
                  :model="infoForm" :rules="rules" v-bind="layout">
      <a-form-model-item has-feedback label="" prop="projectName">
        <a-input v-model="infoForm.projectName"/>
      </a-form-model-item>
      <a-form-model-item has-feedback label="" prop="description">
        <a-input v-model="infoForm.description" type="textarea"
                 style="resize: none; height: 80px "/>
      </a-form-model-item>
      <a-form-model-item has-feedback label="" prop="needPeople">
        <a-input v-model="infoForm.needPeople"/>
      </a-form-model-item>
      <a-form-model-item has-feedback label="" prop="category">
        <a-select v-model="infoForm.category" placeholder="please select your zone">
          <a-select-option value=""></a-select-option>
          <a-select-option value=""></a-select-option>
          <a-select-option value=""></a-select-option>
        </a-select>
      </a-form-model-item>
      <a-form-model-item has-feedback label="" prop="value">
        <a-input v-model="infoForm.value"/>
      </a-form-model-item>
      <a-form-model-item has-feedback label="" prop="workTime">
        <a-input v-model="infoForm.workTime"/>
      </a-form-model-item>
      <a-form-model-item has-feedback label="" prop="address">
        <a-input v-model="infoForm.address"/>
      </a-form-model-item>
      <a-form-model-item has-feedback label="" prop="contactPersonName">
        <a-input v-model="infoForm.contactPersonName"/>
      </a-form-model-item>
      <a-form-model-item has-feedback label="" prop="contactPersonSex">
        <a-radio-group v-model="infoForm.contactPersonSex" default="male">
          <a-radio value='male'>
            
          </a-radio>
          <a-radio value="female">
            
          </a-radio>
        </a-radio-group>
      </a-form-model-item>
      <a-form-model-item has-feedback label="" prop="contactPersonPhone">
        <a-input v-model="infoForm.contactPersonPhone"/>
      </a-form-model-item>
      <a-form-model-item label="">
        <a-form-model-item style="display: inline-block; margin-left: 0"
                           has-feedback prop="startTime">
          <a-date-picker
              v-model="infoForm.startTime"
              type="date"
              placeholder=""
              style="width: 100%;"
              show-time
          />
        </a-form-model-item>
        <span style="display: inline-block; width: 48px; text-align: center">
          
        </span>
        <a-form-model-item style="display: inline-block"
                           has-feedback prop="endTime">
          <a-date-picker
              v-model="infoForm.endTime"
              type="date"
              placeholder=""
              style="width: 100%;"
              show-time
          />
        </a-form-model-item>
      </a-form-model-item>
      <a-form-model-item :wrapper-col="{ span: 12 , offset: 6}">
        <a-button type="primary" @click="onSubmit">
          
        </a-button>
        <a-button style="margin-left: 30px;" @click="resetForm">
          
        </a-button>
      </a-form-model-item>
    </a-form-model>
  </div>
</template>

<script>
import moment from 'moment';
import deepCopy from '../../../../utils/deepCopy';

export default {
  name: 'fillInformation',
  created() {
    if (this.$store.state.addProject.nowInfo.projectName) {
      this.infoForm = deepCopy(this.$store.state.addProject.nowInfo);
      this.infoForm.startTime = moment(this.infoForm.startTime);
      this.infoForm.endTime = moment(this.infoForm.endTime);
    }
  },
  data() {
    const validateProjectName = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    const validateDescription = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (value.length < 50) {
        callback(new Error('50'));
      } else {
        callback();
      }
    };
    const validateNeedPeople = (rule, value, callback) => {
      const regExp = /^\+?[1-9][0-9]*$/; // 
      if (!regExp.test(value)) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    const validateCategory = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    const validateValue = (rule, value, callback) => {
      const regExp = /^\d+(\.\d+)?$/; // 
      if (!regExp.test(value)) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    const validateWorkTime = (rule, value, callback) => {
      const regExp = /^\d+(\.\d+)?$/; // 
      if (!regExp.test(value)) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    const validateAddress = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    const validateContactPersonName = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    const validateContactPersonPhone = (rule, value, callback) => {
      const phoneReg = /^1[3456789]{1}\d{9}$/;
      if (value === '') {
        callback(new Error(''));
      } else if (!phoneReg.test(value)) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    const validateTime = (rule, value, callback) => {
      if (!value) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    return {
      infoForm: {
        projectName: '',
        description: '',
        needPeople: '',
        category: '',
        value: '',
        workTime: '',
        address: '',
        contactPersonName: '',
        contactPersonSex: 'male',
        contactPersonPhone: '',
        startTime: '',
        endTime: '',
      },
      rules: {
        projectName: [{
          validator: validateProjectName,
          trigger: 'change',
        }],
        description: [{
          validator: validateDescription,
          trigger: 'blur',
        }],
        needPeople: [{
          validator: validateNeedPeople,
          trigger: 'change',
        }],
        category: [{
          validator: validateCategory,
          trigger: 'blur',
        }],
        value: [{
          validator: validateValue,
          trigger: 'change',
        }],
        workTime: [{
          validator: validateWorkTime,
          trigger: 'change',
        }],
        contactPersonName: [{
          validator: validateContactPersonName,
          trigger: 'change',
        }],
        contactPersonPhone: [{
          validator: validateContactPersonPhone,
          trigger: 'change',
        }],
        address: [{
          validator: validateAddress,
          trigger: 'change',
        }],
        startTime: [{
          validator: validateTime,
          trigger: 'change',
        }],
        endTime: [{
          validator: validateTime,
          trigger: 'change',
        }],
      },
      layout: {
        labelCol: { span: 7 },
        wrapperCol: { span: 11 },
      },
    };
  },
  methods: {
    onSubmit() {
      this.$refs.infoForm.validate((valid) => {
        const projectInfo = {
          ...this.infoForm,
        };
        projectInfo.needPeople = +projectInfo.needPeople;
        projectInfo.value = +projectInfo.value;
        projectInfo.workTime = +projectInfo.workTime;
        projectInfo.startTime = projectInfo.startTime.format('YYYY-MM-DD HH:MM');
        projectInfo.endTime = projectInfo.endTime.format('YYYY-MM-DD HH:MM');
        if (valid) {
          this.$emit('next', projectInfo);
        } else {
          this.$message.error('');
        }
        return false;
      });
    },
    resetForm() {
      this.infoForm = {
        projectName: '',
        description: '',
        needPeople: '',
        category: '',
        value: '',
        workTime: '',
        address: '',
        contactPersonName: '',
        contactPersonSex: 'male',
        contactPersonPhone: '',
        startTime: '',
        endTime: '',
      };
      this.$refs.infoForm.resetFields();
    },
  },
};
</script>

