<template>
  <div class="deposit-container">
    <div class="title">
      <div></div>
      <div>
         =    + 1
      </div>
      <div>
        
      </div>
      <div>
          
      </div>
      <div>
        
        <span style="color: #f40">{{ value }}</span>
      </div>
    </div>
    <div class="balance">
      <div></div>
      <div style="color: #f40; font-size: 24px">{{ balance }}</div>
    </div>
    <div class="pay">
      <a-button type="primary" @click="previous"></a-button>
      <a-button type="primary" @click="payMoney"></a-button>
    </div>
  </div>
</template>

<script>
import api from '@/api/addProject';
import deepCopy from '../../../../utils/deepCopy';

export default {
  name: 'deposit',
  created() {
    this.projectInfo = deepCopy(this.$store.state.addProject.nowInfo);
    this.balance = this.$store.state.user.userinfo.userMoney;
  },
  data() {
    return {
      projectInfo: {},
      balance: 0,
    };
  },
  computed: {
    value: {
      get() {
        return (this.projectInfo.needPeople + 1) * this.projectInfo.value;
      },
    },
  },
  methods: {
    previous() {
      this.$emit('previous');
    },
    async payMoney() {
      // 
      const params = {
        appkey: this.$store.state.user.userinfo.appkey,
        ...this.projectInfo,
        payMoney: this.value,
      };
      await api.addNewProject(params)
        .then(() => {
          this.$message.success('', 1.5);
        })
        .catch((err) => {
          this.$message.error(err);
        });
      this.$emit('pay');
      this.$store.dispatch('addProject/deleteInfo');
    },
  },
};
</script>

<style scoped lang="less">
.title,
.balance {
  > div {
    &:first-child {
      font-size: 18px;
    }

    font-size: 16px;
    margin-bottom: 20px;
  }
}

.pay {
  button:first-child {
    margin-right: 100px;
  }
}
</style>
