<script>
import api from '@/api/reviewer';

export default {
  name: 'applyReviewer',
  data() {
    return {
      labelCol: { span: 6 },
      wrapperCol: { span: 14 },
      form: {
        name: '',
        identity_number: '',
        resource: '',
        desc: '',
        xuanyan: '',
        declaration: '',
      },
      rules: {
        name: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
        xuanyan: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
        indentity_number: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
        resource: [
          {
            required: true,
            trigger: 'change',
          },
        ],
        declaration: [
          {
            required: true,
            message: '',
            trigger: 'change',
          },
        ],
        desc: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
      },
    };
  },
  methods: {
    onSubmit() {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          const tempObj = {
            appkey: this.$store.state.user.userinfo.appkey,
            ...this.form,
          };
          api.applyToBeReviewer(tempObj);
        } else {
          return false;
        }
        return true;
      });
    },
    resetForm() {
      this.$refs.ruleForm.resetFields();
    },
    onChange() {
    },
  },
};
</script>

